body {
  padding:0;
  margin: 0;
  font-family: Century Gothic;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;  
}
header{
  padding-top: 20px;
  background-color: #bafdfd;
  padding-bottom: 10px;
  width: 100%;
  height: 15%;  
  display: flex;
  justify-content: space-around;
  font-family: Comic Sans MS;
  align-items: center;
}
.logo-bar{
  margin-left: 100px;
  display: flex;
}
.logo-text{  
  font-size: 20pt;
  margin-left: 20px;
  text-shadow: 10px 10px 5px #363636;
}
.logo{
  width: 75px;
  cursor: pointer;
}
.navigations-text{
  font-size: 20pt;
  color: black;
}
.wrapper{
  border-top: 2px double black;
  position: relative;
  width: 100%;
  height: 88.6vh;
  overflow: hidden;
}
.background{ 
  position: absolute; 
  width: 100%;
  height: 88.6vh;
  background-color: #bafdfd;
  z-index: -100;
}
.backgtound-picture{
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0.7;
}
.wrapper-block{
  position: absolute;
  width: 100%;
  height: 88.6vh;
  color: black; 
  opacity: 0.8;
}
.songs-bar{  
  overflow:auto;
  margin: 100px 200px;
  width: 600px;
  height: 60%;     
  opacity: 0.9;
}
.about-page{
  
}
.about-wrapper{
  margin-top: -22px; 
  width: 100%;
  height: 87vh;
}
.about-wrapper::after {
  content: "";
  background-image: url("./picture//background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-y: -200px ;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;   
}
.about-text{
  margin: 100px 30%;
}
.about-gif{
  position: absolute;
  left: 10%;
}
.gif{
  margin-right: 20px;
  display: inline;
  width: 200px;
}
.input{
  position: absolute;
  right: 100px;
  bottom: 200px;
}
button{
  margin-left: 20px;
}
